import { getByText, render, waitFor } from '@testing-library/preact';
import userEvent from '@testing-library/user-event';
import { ComponentProps } from 'preact/compat';
import { expect } from 'chai';
import { BarChart } from '../';
import { setupUseTextDimensionsDependenciesMock } from '../../../test/mocks/mockUseTextDimensionsDependencies';
import { matchTranslationBundle } from '../../utils/UNSAFE_matchTranslationBundle';
import { RootEnvironment, RootEnvironmentProvider } from '../../UNSAFE_Environment';
import { gdpData } from '../../UNSAFE_BarChart/__docs__/data/gdpData';
import { checkTooltipText, queryAllBy } from '../../utils/PRIVATE_visTestUtils';
import { getClientHints } from '../../utils/PRIVATE_clientHints';

const IS_CHROME_HEADLESS = navigator?.userAgent?.includes('HeadlessChrome');
const TOOLTIP_TEST_TIMEOUT = 5000;

type BarChartProps = ComponentProps<typeof BarChart>;

const series = [
  {
    name: 'Series 1',
    id: 'Series 1',
    items: [
      {
        id: '1',
        value: 1
      },
      {
        id: '2',
        value: 2
      }
    ]
  }
];

const setupBarChart = async (
  props: BarChartProps = {
    width: '500px',
    height: '500px',
    series,
    groups: [{ id: 'Group1' }, { id: 'GroupB' }],
    getDataItem: (i: number, j: number) => series[i].items[j],
    'aria-label': 'ariaText'
  }
) => {
  const locale = matchTranslationBundle(['en'], new Set(['en-US', 'en']));
  const { default: translations } = await import(`../../resources/nls/${locale}/bundle.ts`);
  const env: Partial<RootEnvironment> = {
    translations: { '@oracle/oraclejet-preact': translations }
  };
  const chart = <BarChart {...props}></BarChart>;

  return render(<RootEnvironmentProvider environment={env}>{chart}</RootEnvironmentProvider>);
};

describe('Test BarChart component', () => {
  beforeEach(() => {
    setupUseTextDimensionsDependenciesMock(window);
  });

  it('render', async function () {
    const content = await setupBarChart();
    const ariaRole = content.getByRole('application');
    expect(ariaRole).not.null;
  });

  const clientHints = getClientHints();
  if (typeof jest === 'undefined') {
    if (clientHints.browser === 'chrome') {
      mocha.timeout(TOOLTIP_TEST_TIMEOUT);

      if (!IS_CHROME_HEADLESS) {
        it('handles text rotation and truncation', async function () {
          const items = gdpData.map((i, index) => ({
            id: index,
            value: i.gdp
          }));

          const content = await setupBarChart({
            width: '1000px',
            height: '300px',
            series: [
              {
                id: 0,
                name: 'GDP',
                items
              }
            ],
            getDataItem: (_, gIndex) => items[gIndex],
            groups: gdpData.map((i) => ({
              id: i.country
            }))
          });

          // TODO:JET-65258
          // workaround for test issue JET-65258
          // some dom are missing from chart if we do not add
          // this await userEvent.tab() and hence tests to check
          // dom will fail; maybe it's a delay issue.
          await userEvent.tab();
          await userEvent.tab();
          const chart = content.getByRole('application');
          const queryAllByDataObject = queryAllBy('data-oj-text');
          const text = queryAllByDataObject(chart, 'Bosnia and Herzegovina');
          expect(text[0].textContent).equals('Bosnia and Hâ€¦');
          await userEvent.hover(text[0]);
          await waitFor(() => checkTooltipText('Bosnia and Herzegovina'), {
            timeout: 5000
          });
        });
      }
    }

    it('drag mode buttons', async function () {
      const content = await setupBarChart({
        dragMode: 'user',
        zoomAndScroll: 'live',
        selectionMode: 'multiple',
        width: '500px',
        height: '500px',
        series,
        groups: [{ id: 'Group1' }, { id: 'GroupB' }],
        getDataItem: (i: number, j: number) => series[i].items[j],
        'aria-label': 'ariaText'
      });

      const chart = content.getByRole('application');
      expect(chart).not.null;
      const queryAllByGroupIndex = queryAllBy('data-oj-group-index');
      const firstBar = queryAllByGroupIndex(chart, '0')[0];
      await userEvent.hover(firstBar as Element);
      await waitFor(() => {
        const buttons = content.getByRole('toolbar');
        expect(buttons).not.null;
        expect(buttons.children.length).to.equal(2);
        if (clientHints.platform === 'mac' || clientHints.platform === 'windows') {
          expect(getByText(chart, 'Marquee zoom')).not.null;
          expect(getByText(chart, 'Marquee select')).not.null;
        } else if (clientHints.deviceType === 'phone') {
          expect(getByText(chart, 'Pan')).not.null;
          expect(getByText(chart, 'Marquee select')).not.null;
        }
      });
    });
  }

  it('Renders data label', async function () {
    const content = await setupBarChart({
      width: '500px',
      height: '500px',
      getDataItem: (i: number, j: number) => ({
        ...series[i].items[j],
        labelPosition: 'center',
        label: series[i].items[j].value.toString()
      }),
      series,
      groups: [{ id: 'Group1' }, { id: 'GroupB' }]
    });
    const chart = content.getByRole('application');

    // TODO:JET-65258
    // workaround for test issue JET-65258
    // some dom are missing from chart if we do not add
    // this await userEvent.tab() and hence tests to check
    // dom will fail; maybe it's a delay issue.
    await userEvent.tab();
    await userEvent.tab();

    const label1 = getByText(chart, '2');
    const label2 = getByText(chart, '1');
    expect(label1).to.be.not.null;
    expect(label2).to.be.not.null;
  });
});
